.credit-card-wrapper {
  background-color: #252e3a;
  border-radius: 4px;
  min-height: 216px;
  font-size: 14px;
  line-height: 18px;
  color: white;
  background-image: linear-gradient(113.99228deg, #252e3a 4.92708%, #475463 97.84375%);
  box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.22);
  margin-top: 20px;
  padding: 35px 40px 50px 40px; }
  @media (max-width: 767px) {
    .credit-card-wrapper {
      padding: 30px 15px; } }
  @media (max-width: 575px) {
    .credit-card-wrapper {
      min-height: auto; } }

.credit-card-wrapper input.form-control.has-error {
  box-shadow: inset 0 0 0 2px #fb3737; }

.credit-card-wrapper .form-control {
  display: block;
  width: 100%;
  height: 40px;
  padding: 6px 12px;
  font-size: 16px;
  line-height: 1.428571429;
  background-color: #fff;
  background-image: none;
  border: none;
  border-radius: 4px;
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
  transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s; }
  @media (max-width: 575px) {
    .credit-card-wrapper .form-control {
      font-size: 12px;
      height: 35px;
      padding: 5px; } }

.credit-card-wrapper label {
  display: inline-block;
  margin-bottom: 10px; }

.credit-card-wrapper input::-webkit-input-placeholder {
  color: gray;
  font-weight: 300; }

.credit-card-wrapper input:-moz-placeholder {
  color: black; }

.credit-card-wrapper input:-moz-placeholder {
  color: black; }

.credit-card-wrapper input:-ms-input-placeholder {
  color: black; }

.credit-card-wrapper input:focus {
  outline: none;
  box-shadow: 0 0 0 2px gold; }

.billing-address-name {
  text-transform: uppercase; }

.credit-card-wrapper .form-group {
  margin-bottom: 30px; }

